import React from 'react';
import ReactDOM from 'react-dom';

async function loadData() {
  const response =  await fetch(window.UI_API_ENDPOINT, {
    method:'POST',
    headers:{'content-type':'application/json'}
    ,body:JSON.stringify({query:`mutation addEmployee{
      addEmployee(input:
      {
        empid:input.empid,
        fullName:input.fullName
        address:input.address,
        age:input.age,
        email:input.email,
        phoneNumber:input.phoneNumber
      })
    
    }`})
})
  
const rsponseBody =  await response.json();
console.log(rsponseBody);
return rsponseBody.data;
}

export default class AddEmployee extends React.Component{
constructor(props){
    super(props);
    this.state = {lists : []}
    }
    showData = ()=> {
    loadData().then(result => {
        // update the state using setState by assign result to list key.
        this.setState({lists: result}) 
        console.log(result);
            })
    }

    Display = (props) => {
      var rows = [];
      console.log(props)
      if (props.addEmployee) {

        props.addEmployee.map(function(currentelement, index) {
          rows.push(<tr key={index}><td>{props.addEmployee[index].empid}</td>
          <td>{props.addEmployee[index].fullName}</td>
          <td>{props.addEmployee[index].address}</td>
          <td>{props.addEmployee[index].age}</td>
          <td>{props.addEmployee[index].email}</td>
          <td>{props.addEmployee[index].phoneNumber}</td>
          </tr>);
          });
      }
  
      return rows;

  }
  render() { 
  const details = "Employee Details:";
  return (
    <div title="Outer div" className="nav-bar">
    <button type ="submit" className="button" onClick={()=>this.showData() }>Add Employee</button>&nbsp;&nbsp;&nbsp;
    <h2>{details}</h2>
    <table>
    <thead>
      <tr>
        <th>Employee Id</th>
      <th>Emp Name</th>
      <th>Address</th>
      <th>Age</th>
      <th>Email</th>
      <th>Contact Number</th>
      </tr></thead>
    <tbody>{this.Display(this.state.lists)}</tbody>
    </table>
    </div>
  );     
  }

}

const element = <AddEmployee />;
  
  ReactDOM.render(element, document.getElementById('contents'));